<template>
  <div class="my">
    <div class="bg"></div>
    <div class="bow" v-if="isLogin">
      <div class="bow-top clearfix">
        <div class="head-img fl">
          <img :src="userInfo.userImg" alt class="auto-img imghead" />
        </div>
        <div class="shezhi fr" @click="showPopup">
          <img src="../../assets/images/shezhi.png" alt class="auto-img ig" />
        </div>
      </div>
      <div class="name">{{userInfo.nickName}}</div>
    </div>

    <div class="bow" v-else>
      <div class="bow-top clearfix">
        <div class="head-img fl">
          <img :src="userInfo.userImg" alt class="auto-img imghead" />
        </div>
        <div class="shezhi fr" @click="showPopup">
          <img src="../../assets/images/shezhi.png" alt class="auto-img ig" />
        </div>
      </div>
      <div class="name" @click="gologin">请先登录</div>
    </div>
    <van-popup v-model="show" position="left" :style="{ height: '100%' ,width:'70%'}">
      <div class="personal">
        <div class="list-box">
          <van-cell title="头像">
            <div class="avatar fr">
              <img class="auto-img" :src="userInfo.userImg" alt />
              <van-uploader class="uploader-box" :after-read="headimg" />
            </div>
          </van-cell>
          <van-cell title="用户id">
            <div class="fr">{{userInfo.userId}}</div>
          </van-cell>
          <van-cell title="手机号">
            <div class="fr">{{userInfo.phone}}</div>
          </van-cell>
          <van-cell title="昵称">
            <div class="fr">
              <input
                class="nickname"
                type="text"
                v-model="userInfo.nickName"
                @change="modifyuserinfo('nickName', '/updateNickName')"
              />
            </div>
          </van-cell>

          <div class="desc-box">
            <div class="desc-title">简介</div>
            <div>
              <textarea
                v-model="userInfo.desc"
                class="text-area"
                placeholder="这家伙很懒，什么都没有留下"
                @change="modifyuserinfo('desc', '/updateDesc')"
              ></textarea>
            </div>
          </div>
        </div>
      </div>
    </van-popup>
    <div class="myicon clearfix">
      <div class="myicon-item fl" @click="culture">
        <div class="my-icon">
          <img src="../../assets/images/myicon1.png" alt class="auto-img" />
        </div>
        <div class="myicon-info">文化</div>
      </div>
      <div class="myicon-item fl" @click="like">
        <div class="my-icon">
          <img src="../../assets/images/myicon2.png" alt class="auto-img" />
        </div>
        <div class="myicon-info">收藏</div>
      </div>
      <div class="myicon-item fl" @click="me">
        <div class="my-icon">
          <img src="../../assets/images/myicon3.png" alt class="auto-img" />
        </div>
        <div class="myicon-info">我们</div>
      </div>
    </div>
    <div class="other">
      <van-notice-bar mode="link" text="red" color="black" @click="administration">地址管理</van-notice-bar>
    </div>
    <div class="other">
      <van-notice-bar mode="link" text="red" color="black" @click="order">购物清单</van-notice-bar>
    </div>
    <div class="other">
      <div class="you">
        <!-- 优惠券单元格 -->
        <van-coupon-cell :coupons="coupons" :chosen-coupon="chosenCoupon" @click="showList = true" />
        <!-- 优惠券列表 -->
        <van-popup v-model="showList" round position="bottom" style="height: 60%; ">
          <van-coupon-list
            :coupons="coupons"
            :chosen-coupon="chosenCoupon"
            :disabled-coupons="disabledCoupons"
            @change="onChange"
            @exchange="onExchange"
          />
        </van-popup>
      </div>
    </div>
    <div class="other">
      <van-notice-bar mode="link" text="red" color="black" @click="safe">安全中心</van-notice-bar>
    </div>
    <div class="other">
      <van-notice-bar mode="link" text="red" color="black" @click="edition">版本更新</van-notice-bar>
    </div>
  </div>
</template>

<script>
import "../../assets/less/my.less";
import { utils } from "../../assets/js/utils";
const coupon = {
  available: 1,
  condition: "无使用门槛\n最多优惠12元",
  reason: "",
  value: 150,
  name: "优惠券名称",
  startAt: 1599114000,
  endAt: 1600592000,
  valueDesc: "1.5",
  unitDesc: "元",
};
export default {
  data() {
    return {
      show: false,
      userInfo: {},
      maxSize: 1 * 1024 * 1024,
      isLogin: false,
      chosenCoupon: -1,
      coupons: [coupon],
      disabledCoupons: [coupon],
      showList: false,
    };
  },
  created() {
    this.getuserinfo();
  },
  methods: {

    //个人资料展示
    showPopup() {
      this.show = true;
    },

    //获取个人资料
    getuserinfo() {
      //获取token字符串
      let tokenString = localStorage.getItem("CSTK");
      if (!tokenString) {
        return;
      }

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });

      this.axios({
        method: "GET",
        url: "/findAccountInfo",
        params: {
          appkey: this.appkey,
          tokenString,
        },
      })
        .then((result) => {
          this.$toast.clear();
          
          if (result.data.code == "B001") {
            this.isLogin = true;
            this.userInfo = result.data.result[0];
            
          }
        })
        .catch((err) => {
          this.$toast.clear();
          
        });
    },

    //上传头像
    headimg(file) {
      //获取token字符串
      let tokenString = localStorage.getItem("CSTK");
      if (!tokenString) {
        return;
      }

      //获取文件类型
      
      let type = file.file.type.split("/")[1];
      let imgTypes = ["png", "gif", "jpg", "jpeg"];
      if (imgTypes.indexOf(type) === -1) {
        this.$notify({
          message: "图片类型只支持" + imgTypes.join(","),
          color: "#fff",
          background: "#5f56ff",
        });

        return;
      }

      //大小限制
      if (this.maxSize < file.file.size) {
        this.$notify({
          message: "上传文件大小不能超过" + this.maxSize / 1024 + "KB",
          color: "#fff",
          background: "#5f56ff",
        });
        return;
      }

      //获取base64
      let base64 = file.content.replace(/data:image\/[A-Za-z]+;base64,/, "");
      

      //参数序列化
      let data = utils.queryString({
        appkey: this.appkey,
        tokenString,
        imgType: type,
        serverBase64Img: base64,
      });

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });

      this.axios({
        method: "POST",
        url: "/updateAvatar",
        data,
      })
        .then((result) => {
          this.$toast.clear();
          
          if (result.data.code == "H001") {
            this.userInfo.userImg = result.data.userImg;
          }
        })
        .catch((err) => {
          this.$toast.clear();
          
        });
    },

    //修改用户信息
    modifyuserinfo(key, url) {
      //获取token字符串
      let tokenString = localStorage.getItem("CSTK");
      if (!tokenString) {
        return;
      }

      //参数序列化
      let data = utils.queryString({
        appkey: this.appkey,
        tokenString,
        [key]: this.userInfo[key],
      });

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });

      this.axios({
        method: "POST",
        url,
        data,
      })
        .then((result) => {
          this.$toast.clear();
          
          this.$toast(result.data.msg);
        })
        .catch((err) => {
          this.$toast.clear();
          
        });
    },

    //登录
    gologin() {
      this.$router.push({ name: "Login" });
    },

    //地址管理
    administration() {
      this.$router.push({ name: "Address" });
    },

    //购物清单
    order() {
      this.$router.push({ name: "Order" });
    },

    //文化
    culture() {
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
      this.$router.push({ name: "Culture" });
      setTimeout(() => {
        this.$toast.clear();
      }, 500);
    },

    //我们
    me() {
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
      this.$router.push({ name: "Me" });
      setTimeout(() => {
        this.$toast.clear();
      }, 500);
    },

    //收藏
    like() {
      this.$router.push({ name: "Like" });
    },

    //安全中心
    safe() {
      this.$router.push({ name: "Safe" });
    },

    //优惠券
    onChange(index) {
      this.showList = false;
      this.chosenCoupon = index;
    },
    onExchange(code) {
      this.coupons.push(coupon);
    },

    //版本更新
    edition() {
      this.$toast("你已经是最新版本4.1.3,无需更新");
    },
  },
};
</script>

<style lang="less" scoped>
</style>